@import url('https://fonts.googleapis.com/css2?family=Inter:wght@300;400;700;900&family=Lato:wght@400;900&family=Playfair+Display+SC:wght@400;700;900&family=Poppins:ital,wght@0,600;1,600&family=Raleway:ital,wght@0,100;0,200;0,300;1,100;1,200&family=Ubuntu:wght@300;400;700&display=swap');

body {
  
    font-family: 'Poppins', sans-serif;
    
}
p, h1, h2 {
    margin: 0px;
    padding: 0px;
    text-decoration:none;
}
a{
    text-decoration:none;
}
.container {
    background-image: url(../../assets/images/Background.svg);
    backdrop-filter: blur(20px);
    background-color: #121829;
    background-size:cover;
    background-repeat:no-repeat;
    min-height:100vh;
}

ul{
    padding:0px;
    margin:0px;
}
li{
    list-style:none;
}